@font-face {
    font-family: 'Idealist';
    src: url('/assets/fonts/subset-Idealist-Regular.woff2') format('woff2'),
        url('/assets/fonts/subset-Idealist-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Change';
    src: url('/assets/fonts/Change-Regular.woff2') format('woff2'),
        url('/assets/fonts/Change-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Change';
    src: url('/assets/fonts/Change-Bold.woff2') format('woff2'),
        url('/assets/fonts/Change-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Change';
    src: url('/assets/fonts/Change-Italic.woff2') format('woff2'),
        url('/assets/fonts/Change-Italic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

:root {
    --font-stack-body: 'Change', Georgia, 'Times New Roman', Times, serif;
    --font-stack-heading: 'Idealist', Georgia, 'Times New Roman', Times, serif;

    --color-1: #fff;
    --color-2: #424242;
    --color-3: #a95d2f;
}

main {
    max-width: 80%;

    font-size: 1em;

    p {
        max-width: 35em;
        margin-left: auto;
        margin-right: auto;
    }
}

header {
    margin-top: 0;
    text-align: center;

    p {
        font-style: italic;
    }
}

h1 {
    font-size: 4em;
    font-weight: normal;
}

h2 {
    font-family: 'Change', Georgia, 'Times New Roman', Times, serif;
    font-size: 3em;
}

figure {
    max-width: 35em;
    margin-left: auto;
    margin-right: auto;
}

#testimonial {
    img {
        border-radius: 50%;
        shape-outside: ellipse();
        padding: 1em;

        max-width: 20em;

        margin-left: auto;
        margin-right: auto;
    }
}

@media (min-width: 1000px) {
    #testimonial {
        img {
            max-width: 15em;
            float: right;
            margin-top: 0;
        }
    }
}